'use client'

import { useSession } from 'next-auth/react'
import { useEffect, useState } from 'react'

export default function MealsPage() {
  const { data: session, status } = useSession()

  const [myScrollPosition, setMyScrollPosition] = useState(0)
  const [friendsScrollPosition, setFriendsScrollPosition] = useState(0)

  const CARD_WIDTH = 236; // Card size + gap
  const totalMyMeals = 5;
  const totalFriendsMeals = 10;

  // Scroll My Meals
  useEffect(() => {
    const interval = setInterval(() => {
      setMyScrollPosition((prev) => {
        if (prev >= CARD_WIDTH * (totalMyMeals - 1)) {
          return 0
        }
        return prev + CARD_WIDTH
      })
    }, 3000)

    return () => clearInterval(interval)
  }, [])

  // Scroll Friends Meals
  useEffect(() => {
    const interval = setInterval(() => {
      setFriendsScrollPosition((prev) => {
        if (prev >= CARD_WIDTH * (totalFriendsMeals - 1)) {
          return 0
        }
        return prev + CARD_WIDTH
      })
    }, 3000)

    return () => clearInterval(interval)
  }, [])

  if (status === 'loading') return <p>Loading...</p>
  if (status === 'unauthenticated') return <p>You must be logged in to view this page.</p>

  return (
    <div className="flex flex-col min-h-screen bg-gray-50">
      {/* Top Profile Section */}
      <div className="flex w-full p-6 bg-gradient-to-r from-blue-500 to-purple-600 text-white">
        {/* Left side: Profile Pic + Info */}
        <div className="flex flex-col items-center w-1/3 space-y-4">
          <img
            src={session?.user?.image || 'https://via.placeholder.com/120'}
            alt="Profile Picture"
            className="w-32 h-32 rounded-full border-4 border-white object-cover"
          />
          <h2 className="text-3xl font-bold">{session?.user?.name || 'Meal Planner'}</h2>

          {/* Followers/Following */}
          <div className="flex space-x-6 mt-2">
            <div className="text-center">
              <h3 className="text-xl font-bold">150</h3>
              <p className="text-sm">Followers</p>
            </div>
            <div className="text-center">
              <h3 className="text-xl font-bold">180</h3>
              <p className="text-sm">Following</p>
            </div>
          </div>
        </div>

        {/* Right side: Bio/Description */}
        <div className="flex flex-col justify-center w-2/3 pl-10">
          <h3 className="text-xl font-semibold mb-2">About Me</h3>
          <p className="text-md">
            talati is soooo gay suck my dick and big d has a small dick üçΩÔ∏è
          </p>
        </div>
      </div>

      {/* Bottom Feed Section */}
      <div className="flex flex-col items-center mt-8 px-4 w-full">
        {/* My Meals Carousel */}
        <h2 className="text-2xl font-bold mb-6">My Meals</h2>
        <div className="relative w-full max-w-6xl overflow-hidden mb-12">
          <div
            className="flex transition-transform duration-700 ease-in-out gap-4"
            style={{
              transform: `translateX(-${myScrollPosition}px)`,
            }}
          >
            {Array.from({ length: totalMyMeals }).map((_, index) => (
              <div
                key={index}
                className="flex-shrink-0 bg-white p-4 rounded-lg shadow-md flex flex-col items-center w-[220px]"
              >
                <img
                  src="https://via.placeholder.com/200"
                  alt="My Meal"
                  className="rounded-lg mb-4 w-full h-48 object-cover"
                />
                <h3 className="text-lg font-bold mb-2">My Meal {index + 1}</h3>
                <p className="text-gray-600 text-center">Homemade delicious meal!</p>
              </div>
            ))}
          </div>
        </div>

        {/* Friends' Meals Carousel */}
        <h2 className="text-2xl font-bold mb-6">Meals by Friends</h2>
        <div className="relative w-full max-w-6xl overflow-hidden pb-4">
          <div
            className="flex transition-transform duration-700 ease-in-out gap-4"
            style={{
              transform: `translateX(-${friendsScrollPosition}px)`,
            }}
          >
            {Array.from({ length: totalFriendsMeals }).map((_, index) => (
              <div
                key={index}
                className="flex-shrink-0 bg-white p-4 rounded-lg shadow-md flex flex-col items-center w-[220px]"
              >
                <img
                  src="https://via.placeholder.com/200"
                  alt="Friend's Meal"
                  className="rounded-lg mb-4 w-full h-48 object-cover"
                />
                <h3 className="text-lg font-bold mb-2">Friend's Meal {index + 1}</h3>
                <p className="text-gray-600 text-center">Shared by your foodie friends! üç¥</p>
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  )
}
